.logo {
    font-size: 1.6rem;
    font-weight: 500;
    padding: .094em .5em;
    position: relative;
    clip-path: polygon(0 0, 100% 0%, 100% 100%, 0% 100%);
    // border: .14em solid $color-mono-dark;

    &::before {
        content: "";
        position: absolute;
        width: 200%;
        height: 200%;
        top: -50%;
        left: -50%;
        background: linear-gradient($color-primary-light 50%, $color-secondary-lighter 50%);
        z-index: -1;
        animation: 8s logo-border-rotate cubic-bezier(0.165, 0.84, 0.44, 1) infinite
    }

    &::after {
        content: "";
        position: absolute;
        background-color: white;
        inset: 3px;
        z-index: -1;
    }

    @keyframes logo-border-rotate {
        0% {
            transform: rotate(0deg);
        }
        17% {
            transform: rotate((360/6)deg);
        }
        34% {
            transform: rotate((360/6*2)deg);
        }
        50% {
            transform: rotate((360/6*3)deg);
        }
        67% {
            transform: rotate((360/6*4)deg);
        }
        84% {
            transform: rotate((360/6*5)deg);
        }
        100% {
            transform: rotate(360deg);
        }
    }

    label + label {
        margin-left: .2em;
    }
}

.underline {
    font-family: $font-1;
}

p .highlight-primary {
    font-weight: 600;
    text-transform: capitalize;
    color: $color-primary-light;
}

p .highlight-secondary {
    font-weight: 600;
    text-transform: capitalize;
    color: $color-secondary-lightest;
}

.mobile-nav {
    display: none;
}

.nav-option {
    font-family: $font-2;
    display: inline-block;
    position: relative;
    cursor: pointer;
    text-transform: uppercase;

    & + & {
        margin-left: 3.2em;
    }

    &::after {
        height: 2px;
        width: 110%;
        position: absolute;
        background-color: $color-secondary-lighter;
        top: -100%;
        left: -5%;
        clip-path: polygon(0 0, 0 0, 0 100%, 0 100%);
        content: "";
        animation: nav-option-hover-reverse .34s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    }

    &:hover::after, &.active {
        animation: nav-option-hover .58s cubic-bezier(0.175, 0.885, 0.32, 1.275);
        clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
    }

    @keyframes nav-option-hover {
        0% {
            clip-path: polygon(0 0, 0 0, 0 100%, 0 100%);
        }
        100% {
            clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
        }
    }

    @keyframes nav-option-hover-reverse {
        0% {
            clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
        }
        100% {
            clip-path: polygon(100% 0, 100% 0, 100% 100%, 100% 100%);
        }
    }
}

.btn-3 {
