.btn-4 {
    font-size: 1rem;
    letter-spacing: -0.3em;
    writing-mode: vertical-rl;
    text-orientation: upright;
    position: relative;
    animation: text-float 1.4s linear infinite;
    &::after {
        content: "";
        position: absolute;
        width: .6em;
        height: .6em;
        border-top: 2px solid white;
        border-right: 2px solid white;
        bottom: -1em;
        left: .2em;
        transform: rotate(135deg);
    }

    @keyframes text-float {
        0% {
            transform: translateY(0%);
        }
        50% {
            transform: translateY(7.5%);
        }
    }
}
  
.intro-msg {
    // text shadow
    position: relative;
    &::after {
        position: absolute;
        content: "Hello . I am Damon";
        letter-spacing: .11em;
        color: transparent;
        top: 100%;
        left: 0;
        width: 100%;
        height: 100%;
        transform: rotateX(180deg);
        text-shadow: 0 0 .65rem $color-secondary-lightest;
    }
}

.gallery {
    width: 198%;
    height: 68vh;
    display: grid;
    grid-gap: 10px;
    grid-template-columns: repeat(auto-fit, minmax(15vw, 1fr));
    grid-auto-rows: calc(65vh/2);
    // transform: translateX(-30%);
    // grid-auto-flow: dense;
    div {
        display: flex;
        cursor: default;
        justify-content: center;
        align-items: center;
        // font-size: 2em;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            box-shadow: 0 2px 16px $color-mono-dark;
            -moz-user-select: none;
            -webkit-user-select: none;
            user-select: none;
        }
    }

    .horizontal {
        grid-column: span 2;
    }
      
    .vertical {
        grid-row: span 2;
    }
    
    .big {
        grid-column: span 2;
        grid-row: span 2;
    }
}

.menu-btn {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    z-index: 5;
    transform: scale(.65);
    cursor: pointer;
    transition: all .3s ease-in-out;
}
.menu-btn__burger {
    width: 50px;
    height: 6px;
    background: $color-secondary;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(137, 224, 255, 0.23);
    transition: all .5s ease-in-out;
}
.menu-btn__burger::before,
.menu-btn__burger::after {
    content: '';
    position: absolute;
    width: 50px;
    height: 6px;
    background: $color-primary;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(137, 224, 255, 0.23);
    transition: all .5s ease-in-out;
}
.menu-btn__burger::before {
    transform: translateY(-16px);
}
.menu-btn__burger::after {
    transform: translateY(16px);
}
/* ANIMATION */
.menu-btn.open .menu-btn__burger {
    transform: translateX(-50px);
    background: transparent;
    box-shadow: none;
}
.menu-btn.open .menu-btn__burger::before {
    transform: rotate(45deg) translate(35px, -35px);
}
.menu-btn.open .menu-btn__burger::after {
    transform: rotate(-45deg) translate(35px, 35px);
}

.no-select {
    -webkit-touch-callout: none; /* iOS Safari */
      -webkit-user-select: none; /* Safari */
       -khtml-user-select: none; /* Konqueror HTML */
         -moz-user-select: none; /* Old versions of Firefox */
          -ms-user-select: none; /* Internet Explorer/Edge */
              user-select: none; /* Non-prefixed version, currently
                                    supported by Chrome, Edge, Opera and Firefox */
  }

.jelly-text {
    @extend .no-select;
    display: inline-block;
    cursor: default;
    text-shadow: 0 1.2em .65rem $color-secondary-lightest;
}

.jelly-animation {
    animation: jelly-transform 1s cubic-bezier(0.175, 0.885, 0.32, 1.275);

    @keyframes jelly-transform {
        0% {
            transform: scaleX(1) scaleY(1);
            color: $color-primary-light;
        }
        34% {
            transform: scaleX(1.45) scaleY(0.6);
            color: rgb(146, 221, 246);
        }
        51% {
            transform: scaleX(.7) scaleY(1.2);
            
        }
        65% {
            transform: scaleX(1.15) scaleY(0.8);
            
        }
        77% {
            transform: scaleX(.85) scaleY(1.15);
        }
        92% {
            transform: scaleX(1.1) scaleY(0.9);
        }
        100% {
            transform: scaleX(1) scaleY(1);
        }
    }
}

.jelly-animation-2 {
    animation: jelly-transform-2 1s cubic-bezier(0.175, 0.885, 0.32, 1.275);

    @keyframes jelly-transform-2 {
        0% {
            transform: scaleX(1) scaleY(1);
            color: $color-secondary-lightest;
        }
        34% {
            transform: scaleX(1.45) scaleY(0.6);
            color: $color-secondary-lighter;
        }
        51% {
            transform: scaleX(.7) scaleY(1.2);
            
        }
        65% {
            transform: scaleX(1.15) scaleY(0.8);
            
        }
        77% {
            transform: scaleX(.85) scaleY(1.15);
        }
        92% {
            transform: scaleX(1.1) scaleY(0.9);
        }
        100% {
            transform: scaleX(1) scaleY(1);
        }
    }
}

.big-d {
    color: $color-secondary-lighter !important;
    text-shadow: -.06em 0 $color-primary-light !important;
}

.bg-load-animation {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    object-fit: cover;
    z-index: 100;
    filter: opacity(0);
    animation: bg-load-animation 2s linear;

    @keyframes bg-load-animation {
        0% {
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            filter: opacity(.5);
        }
        20% {
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            filter: opacity(.5);
        }
        90% {
            width: $content-width;
            height: $content-height;
            top: $content-offset-top;
            left: $content-offset-left;
            filter: opacity(.1);
        }
        100%{
            width: $content-width;
            height: $content-height;
            top: $content-offset-top;
            left: $content-offset-left;
            filter: opacity(0);
        }
    }
}

@media screen and (max-width: 768px) {
    .bg-load-animation {
        display: none;
    }
}

.form-msg {
    animation: msg 3s cubic-bezier(0.075, 0.82, 0.165, 1);
    @keyframes msg {
        0% {
            opacity: 0;
        }
        33% {
            opacity: 1;
        }
        50% {
            opacity: 1;
        }
        100% {
            opacity: 0;
        }
    }
}

.nav-menu {
    position: absolute;
    top: 0;
    right: 0;
    width: 12em;
    height: 22em;
    clip-path: polygon(0 0, 100% 0%, 100% 100%, 0 93%);
    background-color: #dab497;
    animation: menu 1s cubic-bezier(0.075, 0.82, 0.165, 1);
    @keyframes menu {
        0% {
            clip-path: polygon(0 0, 100% 0%, 100% 0, 0 0);
        }
        100% {
            clip-path: polygon(0 0, 100% 0%, 100% 100%, 0 93%);
        }
    }
}

