    }
}

.transparent-bg {
    background-color: #ffffff00;
    animation: transparent-nav .5s ease-in;
    @keyframes transparent-nav {
        0% {
            background-color: #ffffffff;
        }
        100% {
            background-color: #ffffff00;
        }
    }
}

.slide-btn {
