@import '_base.scss';

@include tablet {
    body {
        .middle-section, .page {
            width: $content-width-mobile;
            height: $content-height-mobile;
        }
    
        .middle-section {
            margin-top: 1.5vh;
            overflow-y: scroll;
        }
        #home, #about {
            & {
                padding: Max(15vh, 6.5em) 4.4vw;
                text-align: center;
            }
            .btn-3 {
                margin-top: 12em;
            }
        }
        
        #contact {
            text-align: center;
            .input-field {
                width: 70vw;
                margin: 0 auto 0 auto;
                margin-top: 2.5em;
            }
            .form-msg {
                display: none;
            }
        }
    
        .left-section, .right-section {
            display: none;
        }

        .navbar {
            width: $content-width-mobile - $navbar-offset-x-mobile * 2;
            left: (100vw - $content-width-mobile) / 2 + $navbar-offset-x-mobile;
            top: (100vh - $content-height-mobile) / 2 + $navbar-offset-y;
        }

        .mobile-nav {
            display: block;
        }

        .nav-option {
            display: none;
        }
    }
}

.middle-section {
    width: $content-width;
    height: $content-height;
    margin: auto;
    margin-top: $content-offset-top;
    overflow-y: hidden;
    overflow-x: hidden;
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */

    &::-webkit-scrollbar { /* Chrome, Safari and Opera */
        display: none;
    }
}

.left-section, .right-section {
    position: absolute;
}

.left-section {
    left: 7.65vw;
    color: $color-secondary-light;
    transform-origin: bottom left;
    transform: translateY(23.8em) rotate(-90deg); // w/ phone num
    font-weight: 600;
    font-size: .9rem;
}

.right-section {
    right: 7.65vw;
    bottom: 5vh;
    display: flex;
    flex-direction: column;

}

.nav-side-option {
    padding: .255em;
    background-color: $color-primary-light;
    // border-radius: 3em;
    border: 1px solid black;

    &+& {
        margin-top: .2em;
    }

    &.selected {
        background-color: $color-primary;
    }
}

.page {
    height: 86.2vh;
    padding: Max(15vh, 5em) Min(4.8vw, 6em);
    // allows background-mouse movement
    background-position: 50% 50%;
    background-size: cover;
}

.page > * + * {
    margin-top: 2em;
}

@media screen and (max-width: 1024px) {
    body {
        .page > * + * {
            margin-top: 1em;
        }
    }
}
.navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: absolute;
    padding: 0 1.2em;
    top: (100vh - $content-height) / 2 + $navbar-offset-y;
    left: (100vw - $content-width) / 2 + $navbar-offset-x;
    height: $navbar-height;
    width: $content-width - $navbar-offset-x * 2;
    color: $color-secondary-dark;
    background-color: white;
    animation: show-nav 1s ease-in;
    opacity: .88;
    @keyframes show-nav {
        0% {
            opacity: 0;
        }
        100% {
            opacity: 1;
        }
    }
}

#home {
    background-image: url("../../public/images/wallpapers/cow.jpg");
    animation: home-zoom-in 1s linear;
    a {
        margin-top: 5em;
    }
    h1 {
        color: $color-secondary-lightest;
    }
}

#about {
    color: white;
    background-image: url("../../public/images/wallpapers/wave.jpg");
    p {
        font-weight: 400;
    }
    h1 {
        color: transparent;
        text-shadow: 0 0 0.04rem $color-secondary-lightest;
        text-shadow: 0 0 0.04rem $color-primary-light;
    }
}

#skills {
    background-image: url("../../public/images/wallpapers/city.png");    
    position: relative;
    .btn-4 {
        position: absolute;
        right: 1.65em;
        bottom: 3.3em;
    }

    #mycanvas {
        position: absolute;
        margin-top: 0;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
    }
}

#projects {
    background-image: url("../../public/images/wallpapers/bridge.jpg");    
    cursor: grab;
}

#contact {
    h1 {
        color: $color-secondary-lightest;
        text-shadow: none;
    }
    background-image: url("../../public/images/wallpapers/highway.jpeg");    
}

.swiper {
    width: 100%;
    height: calc($content-height-mobile - $navbar-offset-y * 7);
    .my-slide {
        padding: 0 .3em 2.4em .3em;
        height: 100%;
        width: 100%;
    }
}

@include tablet {
    .swiper {
        margin-top: 2em;
    }
}

#img-to-char {
    background-color: $color-secondary-lightest;
    color: $color-primary !important;
    width: 100%;
    height: 100%;
    padding: 1em 2.6em;
    input {
        display: inline-block;
        color: $color-primary !important;
        border: 1px solid $color-primary !important;
    }
    a {
        color: $color-primary-dark;
        border-bottom: 1px solid $color-primary-dark;
    }
}

#img-to-char-iframe {
    width: 100%;
    height: 85%;
    margin-top: .8em;
    border: 3px solid $color-primary;
}

#mobile-p {
    display: none;
}

@include tablet {
    #img-to-char {
        h3, p {
            display: none;
        }
        #mobile-p {
            display: block;
        }
    }
}

#spacious {
    color: $color-primary-light;
    width: 100%;
    height: 100%;
    position: relative;
    background-color: rgba(50, 50, 50, .7);
    z-index: 5;

    #spacious-desktop {
        width: 100%;
        z-index: 9;
        transition: 1s ease-in-out;
    }
    #spacious-mobile {
        z-index: 10;
        position: absolute;
        width: Max(40%, 5em);
        object-fit: cover;
        opacity: 0;
        transition: 1s ease-in-out;
    }

    &:hover {
        #spacious-desktop {
            opacity: .1;
            width: 20%;
        }
        #spacious-mobile, .content {
            opacity: 1;
        }
    }

    .content {
        z-index: -1;
        position: absolute;
        padding: 2.4em;
        width: 50%;
        text-align: right;
        opacity: .5;
        top: 0;
        right: 0;
        transition: .5s ease-in-out;
        *+* {
            margin-top: 1em;
        }
    }
}

#ecommerce {
    color: $color-primary-light;
    width: 100%;
    height: 100%;
    position: relative;
    background-color: rgba(50, 50, 50, .7);

    .content {
        z-index: 4;
        position: absolute;
        padding: 2.4em;
        width: 50%;
        text-align: right;
        top: 0;
        right: 0;
        transition: .5s ease-in-out;
        *+* {
            margin-top: 1em;
        }
        color: white;
        background-color: #222222cc;
        height: 100%;
    }

    .image-bundle {
        width: 80%;
        width: 80%;
        height: 100%;
        position: relative;
        img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            filter: blur(2px)
        }
        img:nth-child(1) {
            clip-path: polygon(0 0, 100% 0, 100% 35%, 0 65%);
        }
        img:nth-child(2) {
            clip-path: polygon(0 65%, 100% 35%, 100% 100%, 0 100%);
        }
    }
}

@media screen and (max-width: 768px) {
    #ecommerce .content {
        width: 100%;
    }
    #ecommerce .image-bundle {
        width: 100%;
    }
    body {
        #spacious {
            background-color: rgba(50, 50, 50, .7);
            z-index: -1;
            #spacious-mobile {
                display: none;
            }
            img {
                filter: blur(2px);
            }

            .content {
                color: white;
                opacity: 1;
                height: 100%;
                z-index: 10;
                background-color: #222222cc;
                width: 100%;
            }

            &:hover {
                #spacious-desktop {
                    opacity: 1;
                    width: 100%;
                }
            }
        }
    }
}

#computer-graphics {
    // background-color: $color-secondary-lightest;
    width: 100%;
    height: 100%;
}

.graphics-control-panel {
    transform: translateY(-130%);
    * + * {
        margin-top: 2em;
    }
}

@include tablet {
    body {
        .graphics-control-panel {
            font-size: .85em;
        }
    }
}

#attractor-iframe {
    border: none;
}