// colors
$color-primary-dark: #0e2542;
$color-primary: #356673;
$color-primary-light: #daf4fd;

$color-secondary-dark: #42220e;
$color-secondary: #734835;
$color-secondary-light: #886059;
$color-secondary-lighter: #dab497;
$color-secondary-lightest: #ffedd7;

$color-mono-dark: #3d3d3d;
$color-mono-normal: #616060;
$color-mono-light: #f8f8f8;

// font
$font-1: 'Montserrat', sans-serif;
$font-2: 'Bolt';
$font-3: 'EnriqueRound';

$content-width: 81.4vw;
$content-height: 86.2vh;
$content-offset-top: 9.2vh;
$content-offset-left: 9.3vw;
$content-width-mobile: 95vw;
$content-height-mobile: 97vh;
$navbar-offset-x: 1.6vw;
$navbar-offset-y: 4vh;
$navbar-offset-x-mobile: 4vw;
$navbar-offset-y-mobile: 5vh;
$navbar-height: 3.65rem;
$input-width: Min(20em, 90%);

.swiper-pagination-bullet {
    background: rgb(255, 255, 255) !important;
    opacity: .4 !important;
}

.swiper-pagination-bullet-active {
    background: $color-secondary-lighter !important;
    opacity: 1 !important;
}

.swiper-button-prev, .swiper-button-next  {
    color: $color-secondary-dark !important; 
}

@mixin mobile {
    @media screen and (max-width: 480px) {
        @content;
    }
}

@mixin tablet {
    @media screen and (max-width: 768px) {
        @content;
    }
}

