* {
    box-sizing: border-box;
    margin: 0;
    font-family: $font-1;
    scroll-behavior: smooth;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

body {
    background-color: #ebebeb;
    color: $color-mono-light;
    overflow: hidden;
}

form > * + * {
    margin-top: 2em;
}

h1, h2, h3, h4, h5, h6 {
    font-family: $font-2;
    letter-spacing: .12em;
    text-transform: uppercase;
}

h1 {
    font-size: 3.2rem;
}

h3 {
    font-weight: 500;
}

textarea {
    resize: none;
}

p {
    font-size: 1.15rem;
    font-weight: 400;
}

a {
    letter-spacing: .12em;
    text-decoration: none;
    text-transform: uppercase;
    color: inherit;
    cursor: pointer;
}

.input-field {
    position: relative;
    width: $input-width;
}

input[type=text], textarea {
    background-color: transparent;
    color: $color-mono-light;
    font-size: 1rem;
    padding: 0 .4em;
    border: none;
    border-bottom: 2px solid #ffffffaa;
    width: 100%;
    min-height: Min(2.2em, 6.7vh);

    &:invalid:focus {
        border-bottom: 2px solid rgb(233, 99, 99);
    }
    
    &:active, &:focus {
        outline: none;
    }

    &:focus {
        border-bottom: 2px solid $color-mono-light;
    }

    &:hover {
        background-color: #d8d8d811;
    }

    &:valid + .placeholder, &:focus + .placeholder {  
        transform: translateY(-100%);
        opacity: 100%;
    }
}

.placeholder {
    position: absolute;
    letter-spacing: .07em;
    text-transform: capitalize;
    font-family: $font-2;
    font-size: 0.88rem;
    color: #f5f5f5;
    opacity: 60%;
    top: 0;
    left: 0;
    margin: 0;
    padding: 0;
    transition: .2s ease;
}